.number_input_container{
    display: flex;
    flex-direction: column;
    max-width: 100%;
    margin: 20px;
}
.number_label{
    padding: 10px;
}
.number_input_input{
    padding: 15px;
    border-radius: 8px;
    border: .5px solid rgba(0,0,0,.2);
    outline: none;
    margin: 5px 0px;
    width: 100%;
}
